import { Injectable } from '@angular/core';
import * as S3 from 'aws-sdk/clients/s3';
import {MainService} from './main.service';

@Injectable({
  providedIn: 'root',
})
export class S3Service {

  /** Variable that holds S3 buckets */
  bucket: S3;
  /** Backend path that retrieves S3 credentials */
  readonly S3_CREDENTIALS_API = 'credentials/s3-credentials';
  /** S3 Bucket name */
  readonly S3_BUCKET = 'persing';
  /** S3 bucket region */
  readonly S3_BUCKET_REGION = 'us-east-1';
  /** S3 bucket ACL */
  readonly S3_BUCKET_ACL = 'public-read';

  constructor(private mainService: MainService) { }

  /**
   * Method that uploads a file in amazon S3
   * @param param0 File: File the is going to be uploaded / Key: Path used to store the file ->(next line)
   * / Callback: callback that handles S3 response
   */
  async uploadFile({file, key, callback}) {
    this.getS3Credentials().then((response) => {
      const contentType = file.type;
      this.bucket = new S3({
        accessKeyId: response.accessKeyId,
        secretAccessKey: response.secretAccessKey,
        region: this.S3_BUCKET_REGION});
      const params = {
        Bucket: this.S3_BUCKET,
        Key:  key,
        Body: file,
        ACL: this.S3_BUCKET_ACL,
        ContentType: contentType,
      };
      const options = {
        // Part Size of 10mb
        partSize: 10 * 1024 * 1024,
        queueSize: 1,
        // Give the owner of the bucket full control
        ACL: this.S3_BUCKET_ACL,
      };
      this.bucket.upload(params, options, callback);
    });
  }

  /**
   * Deletes an object from S3.
   * @param param0
   */
  async deleteFileS3({key, callback}) {
    this.getS3Credentials().then((response) => {
      this.bucket = new S3({
        accessKeyId: response.accessKeyId,
        secretAccessKey: response.secretAccessKey,
        region: this.S3_BUCKET_REGION});

      const params = {
        Bucket: this.S3_BUCKET,
        Key: key,
      };

      this.bucket.deleteObject(params, callback);
    });
  }

  /**
   * Copies an s3 object from one path to another
   */
  async copyObject({sourcePath, newKey, callback}) {
    this.getS3Credentials().then((response) => {
      this.bucket = new S3({
        accessKeyId: response.accessKeyId,
        secretAccessKey: response.secretAccessKey,
        region: this.S3_BUCKET_REGION});

      const params = {
        Bucket: this.S3_BUCKET,
        CopySource: `${this.S3_BUCKET}/${sourcePath}`,
        Key: newKey,
      };
      this.bucket.copyObject(params, callback);
    });
  }

  /**
   * Generates a random key
   */
  generateKey() {
    let result = '';
    const characters =
      'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    const charactersLength = characters.length;
    for (let i = 0; i < 15; i++) {
      result += characters.charAt(Math.floor(Math.random() * charactersLength));
    }
    return result;
  }

  /**
   * Retrieves s3 api credentials from the backend
   * @return Observable
   */
  private getS3Credentials(): Promise<any> {
    return this.mainService.get({api: this.S3_CREDENTIALS_API});
  }
}
